import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/backtab',
    name: 'Backtab',
    component: () => import('../views/Backtab.vue'),
    children:[
      {
        path:'travelList',
        component: () => import('../views/travelList.vue')
      },
      {
        path:'equikList',
        component: () => import('../views/equikList.vue')
      },
      {
        path:'statistic',
        component: () => import('../views/statistic.vue')
      },
      {
        path:'map/:id',
        component: () => import('../views/map.vue')
      },
      {
        path:'maplen/:id',
        component: () => import('../views/maplen.vue')
      },
      {
        path:'travelList/details/:id',
        component: () => import('../views/traveldetails.vue')
      },
      {
        path:'travelList/tableview/:id',
        component: () => import('../views/tableview.vue')
      },
      {
        path:'travelList/equitview/:id',
        component: () => import('../views/equitview.vue')
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
